	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/imagesizer-is-now-available-from-the-adobe-air-marketplace/index.md absPath of file >>> MarkdownRemark","html":"<p>A couple of days ago Adobe relaunched the AIR Marketplace - the official repository of AIR applications available for download - and with it made things a lot more accessible for 'Publishers' to add their applications to the Marketplace.  I'm not really sure how you got your applications on the previous version of the marketplace, but now you just have to submit yourself as a publisher, get approved, and then submit your AIR application to the Marketplace (which also has to be approved).</p>\n<p>So I'm pleased to announce that ImageSizer is now <a href=\"http://www.adobe.com/cfusion/marketplace/index.cfm?event=marketplace.offering&#x26;offeringid=10740&#x26;marketplaceid=1\">available from the Adobe AIR Marketplace</a>, and we even fixed a few bugs in the process.</p>\n<p><a href=\"http://www.adobe.com/cfusion/marketplace/index.cfm?event=marketplace.offering&#x26;offeringid=10740&#x26;marketplaceid=1\"><img src=\"http://uploads.psyked.co.uk/2009/02/imagesizer.jpg\" alt=\"ImageSizer on the Adobe AIR Marketplace\" title=\"ImageSizer on the Adobe AIR Marketplace\"></a></p>\n<h3>Updates?  Updates you say?</h3>\n<p>Well first off, apologies to anyone who was using ImageSizer regularily - a couple of automatic updates went out recently, and both of them had a few bugs in them.  One made the application fail totally on Multiscreen setups - and the other well, wasn't so critical.</p>\n<p>Anyway, those are fixed now!</p>\n<p>ImageSizer is now at version 0.4.4.7 - in honour of it's release on the AIR Marketplace we've introduced a few more tweaks to the application settings, removed the somewhat annoying alerts at the start, and attempted to make the interface a bit more intuative.  Why don't you download it and try it out now!</p>\n<p><img src=\"http://uploads.psyked.co.uk/2009/02/imagesizer2.jpg\" alt=\"ImageSizer on AIR Marketplace\" title=\"ImageSizer on AIR Marketplace\"></p>","frontmatter":{"title":"ImageSizer is now available from the Adobe AIR Marketplace!","date":"February 13, 2009","tags":["imagesizer","air","air-marketplace"],"url":null}}},"pathContext":{}}

